import { login, register } from "@/api/user";
// 仅在客户端加载第三方包
const Cookie = process.client ? require("js-cookie") : undefined;
export default {
	// 在路由匹配组件渲染之前会执行中间件处理
	middleware: ["noAuth"], //中间件选项：会自动找到middleware目录下的noAuth.js文件，
	name: "LoginIndex",
	//import引入的组件需要注入到对象中才能使用
	components: {},
	props: {},
	data() {
		//这里存放数据
		return {
			user: {
				username: "",
				email: "",
				password: "",
			},
			err: {
				// email:['fdfsd','xxfdfd'],
				// password:['ff','xx']
			}, //错误信息
		};
	},
	//监听属性 类似于data概念
	computed: {
		// 根据路由显示注册/登录
		isLogin() {
			return this.$route.name === "login";
		},
	},
	//监控data中的数据变化
	watch: {},
	//方法集合
	methods: {
		async onSubmt() {
			try {
				// 判断是否是登录还是注册
				const { data } = this.isLogin
					? await login({
							user: this.user,
					  })
					: await register({
							user: this.user,
					  });
				console.log(data);
				// 处理登录成功保护用户的登录状态
				this.$store.commit("setUser", data.user);
				// 为了防止刷新页面数据丢失，我们需要把数据持久化
				Cookie.set("user", data.user);
				// 跳转到首页
				this.$router.push("/");
			} catch (error) {
				console.log("请求失败");
				console.dir(error); //这样才能打印出错误
				this.err = error.response.data.errors;
			}
		},
	},
	//生命周期 - 创建完成（可以访问当前this实例）
	created() {},
	//生命周期 - 挂载完成（可以访问DOM元素）
	mounted() {},
};
